@import url("https://fonts.googleapis.com/css2?family=Dongle&display=swap");

.htable {
  overflow-y: scroll;
  max-height: 60vh;
  background: white;
  padding-left: 20px;
  border-radius: 15px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  display: grid;
  grid-template-columns: 300px 200px 150px 250px 200px ;
}
.htable h3 {
  font-family: Dongle;
  font-size: 45px;
  font-style: normal;
  font-weight: 400;
  line-height: 65px;
  letter-spacing: 0em;
  text-align: left;
}
.htable-name {
  grid-column: 1/2;
}

.htable-code {
  grid-column: 2/3;
}


.htable-taille {
  grid-column: 3/4;
}

.htable-prix {
  grid-column: 4/5;
}
.htable-prix_reste {
  grid-column: 5/6;
  
}


.h-admin {
  display: contents;
}

.h-admin button {
  background-color: rgba(247, 233, 142, 1);
  height: 30px;
  width: 100px;
  font-weight: bold;
}
.h-admin p {
  font-family: Dongle;
  font-size: 35px;
  font-style: normal;
  font-weight: 300;
  line-height: 51px;
  letter-spacing: 0em;
  text-align: left;
}
